@import url('https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap');

* .containercv {
    padding-top: 50px;
    width: 100%;
    max-width: 1000px;
    min-height: 1000px;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
    box-shadow: 0 35px 55px rgba(211, 68, 68, 0.1);
}

.containercv .left_Side {
    background: #800b0d;
    padding: 40px;
}

.profileText {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(179, 46, 46, 0.2);
}

.profileText .imgBx {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    overflow: hidden;

}

.profileText .imgBx img {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.profileText h2 {
    color: #fff;
    font-size: 1.5em;
    margin-top: 20px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    line-height: 1.4em;
}

.profileText h2 span {
    font-size: 0.8em;
    font-style: 300;
}

.contactInfo {
    padding-top: 40px;
}

.title1 {
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 1px;
    margin-bottom: 20px;

}


.contactInfo ul li {
    list-style: none;
    margin: 10px 0;
    cursor: pointer;
}

.contactInfo ul li .icon {
    display: inline-block;
    padding-top: 18px;
    width: 30px;
    font-size: 18px;
    color: #e37b7d;
}

.contactInfo ul li span {
    color: #fff;
    font-weight: 300;
}

.contactInfo.education li {
    margin-bottom: 15px;

}

.contactInfo.education h5 {
    color: rgb(241, 141, 141);
    font-weight: 500;
}

.contactInfo.education h4 {
    color: rgb(252, 248, 248);
    font-weight: 500;
}

.contactInfo.education h4:nth-child(2) {
    color: #fff;
    font-weight: 300;
}

.contactInfo.language .percent {
    width: 100%;
    height: 6px;
    background: #f18384;
    display: block;
    margin-top: 5px;
    border-radius:20px ;

}


.contactInfo.language .percent div {
    top: 0;
    left: 0;
    height: 100%;
    background: #620305;
    border-radius:20px ;

    
}

.containercv .right_Side {
    background: #fff;
    padding: 40px;
}

.about {
    margin-bottom: 50px;

}

.about:last-child {
    margin-bottom: 0;
}

.title2 {
    color: #003147;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 10px;
}

p {
    color: #333;
}

.about .box {
    display: flex;
    flex-direction: row;
    margin: 20px 0;
}

.about .box .year_company {
    min-width: 150px;
}

.about .box .year_company h5 {
    text-transform: uppercase;
    color: #848c90;
    font-weight: 600;
}

.about .box .text h4 {
    text-transform: uppercase;
    color: rgb(148, 8, 8);
    font-size: 16px;
}

.skills .box {
    width: 100%;
    display: grid;
    grid-template-columns: 150px 1fr;
    justify-content: center;
    align-items: center;
}

.skills .box h4 {
    text-transform: uppercase;
    color: #848c99;
    font-weight: 500;

}

.skills .box .percent {
    width: 100%;
    height: 10px;
    background: #c1baba;
    border-radius:20px ;
}

.skills .box .percent div {
    top: 0;
    left: 00;
    height: 100%;
    background: rgb(148, 8, 8);
    border-radius:20px ;

}

.interest ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.interest ul li {
    list-style: none;
    color: #333;
    font-weight: 500;
    margin: 10px 0;
}

.interest ul li .fa {
    color: #800b0d;
    font-size: 18px;
    width: 20px;
}